
import '../../Style.css';
import {Button} from 'antd';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {
    URI_IDX_1,
    URI_HOST,
    URI_MANAGER_BOOKING,
    URI_MANAGER_BIKE,
    URI_USER_DATA_UPDATE,
    URI_MANAGER_COMMENT_UPDATE
} from '../../constants/URIs';
import {
    URL_ADMIN_BOOKING_MANAGEMENT,
    URL_IDX,
    URL_MANAGER_BIKE,
    URL_MANAGER_BOOKING_MANAGEMENT
} from '../../constants/URLs';
import {ERR_UPDATING_COMMENT, ERR_UPDATING_USER} from "../../constants/ErrorMessages";

const Booking = () => {
    const { t } = useTranslation('translations/manager/Booking');
    const navigate = useNavigate();
    const params = useParams();
    const token = useLocation().state.token;

    const [booking, setBooking] = useState('');
    const [comment, setComment] = useState('');
    const [bike, setBike] = useState('');
    const [openCommentEdit, setOpenCommentEdit] = useState(false);

    // GET booking
    const fetchBooking = () => {
        fetch(URI_MANAGER_BOOKING.replace(URI_IDX_1, params.idx), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => { setBooking(data); fetchBike(data.bike)})
            .catch(error => { console.error('Error fetching booking data:', error); });
    }

    // GET bike
    const fetchBike = (bikeId) => {
        fetch(URI_MANAGER_BIKE.replace(URI_IDX_1, bikeId), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => { setBike(data); })
            .catch(error => { console.error('Error fetching bike data:', error); });
    };

    useEffect(() => {
        fetchBooking();
    }, []);

    //  PATCH comment
    const updateComment = () => {
        const payload = {
            content: comment
        }
        console.log('payload', payload)
        fetch(URI_MANAGER_COMMENT_UPDATE.replace(URI_IDX_1, params.idx), {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload),
        })
            .then((response) => response.json())
            .catch((error) => { console.error(ERR_UPDATING_COMMENT, error);});
    }

    const handleEditCommentClick = () => {
        setOpenCommentEdit(true);
    }

    const handleSubmitCommentEditClick = () => {
        updateComment();
        setOpenCommentEdit(false);
    }

    const handleCancelCommentEditClick = () => {
        setOpenCommentEdit(false);
    }

    const handleBikeClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_BIKE.replace(':', '').replace(URL_IDX, bike.id), {state: {token: token}});
        })
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_BOOKING_MANAGEMENT, {state: {token: token}});
        })
    }

    return (

        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>
                <div className={'content-container'}>
                    <h2>{t('booked_dates')}</h2>
                    <p>{t('from')}: {booking.begin}</p>
                    <p>{t('to')}: {booking.end}</p>
                    <p>{t('equipment')}: {booking.equipment?.map(e => e.equipment).toString()}</p>
                    <h2>{t('comment')}</h2>
                    { openCommentEdit ?
                        <>
                            <input
                                className={'text-form'}
                                type='text'
                                placeholder={comment}
                                onInput={e => setComment(e.target.value)}
                            />
                            <div className={'button-list'}>
                                <Button className={'accent-button' } onClick={handleSubmitCommentEditClick}>{t('submit_changes')}</Button>
                                <Button className={'button' } onClick={handleCancelCommentEditClick}>{t('cancel')}</Button>
                            </div>
                        </>
                        :
                        <>
                            { booking.comment ?
                                <p>{booking.comment}</p>
                                :
                                <p>{t('no_comment')}</p>
                            }
                            <div className={'button-list'}>
                                <Button className={'button' } onClick={handleEditCommentClick}>{t('edit_comment')}</Button>
                            </div>

                        </>
                    }
                </div>
                <div className={'clickable-content-container'} onClick={handleBikeClick}>
                    <h2>{bike.name}</h2>
                    <img className={'img'} src={bike.image_link}></img>
                </div>
                <div className={'button-list'}>
                    <Button className={'button'}>{t('hand_out_bike')}</Button>
                    <Button className={'button'}>{t('accept_bike')}</Button>
                    <Button className={'button'}>{t('cancel_booking')}</Button>
                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Booking;