import React, {startTransition, Suspense, useEffect, useState} from 'react';
import {useLocation, useParams} from 'react-router-dom';
import { useNavigate } from 'react-router-dom';
import logo_img from '../../logo.png';
import bike_img from '../../bike.png';
import { Button } from 'antd';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import '../../Style.css';
import { useTranslation } from 'react-i18next';
import LanguageMenu from '../LanguageMenu';
import Loading from '../Loading';
import {
    URI_BOOKING_BIKE, URI_BOOKING_BIKE_STORE,
    URI_IDX_1,
    URI_USER_BOOKED_BIKE,
    URI_USER_BOOKED_STORE
} from "../../constants/URIs";
import {
    URL_BOOKING_CHECKOUT, URL_BOOKING_FILTER,
    URL_BOOKING_STORE,
    URL_IDX,
    URL_USER_LOGIN,
    URL_USER_REGISTER
} from "../../constants/URLs";
import Select from "react-select";

const Bike = () => {
    const { t } = useTranslation('translations/booking/Bike');
    const navigate = useNavigate();
    const params = useParams();
    const token = useLocation().state.token;

    const [bike, setBike] = useState('');
    const [store, setStore] = useState('');

    const [selectedDateMargins, setSelectedDateMargins] = useState([]);

    const [openLoginSignInDialog, setOpenLoginSignInDialog] = useState(false);

    const [allEquipmentOptions, setAllEquipmentOptions] = useState([])
    const [selectedEquipmentOptions, setSelectedEquipmentOptions] = useState([]);
    const [selectedEquipment, setSelectedEquipment] = useState([]);


    // GET bike
    const fetchBike = () => {
        fetch(URI_BOOKING_BIKE.replace(URI_IDX_1, params.idx))
            .then(response => response.json())
            .then(data => {
                console.log('data:', data)
                setBike(data);
                setAllEquipmentOptions(data.equipment.map((equipment) => (
                    {value: equipment.equipment, label: equipment.equipment}
                )));
                console.log('token:', token)
                fetchStore(data.store)
            })
            .catch(error => {
                console.error('Error fetching bike data:', error);
            });
    };

    // GET store
    const fetchStore = () => {
        fetch(URI_BOOKING_BIKE_STORE.replace(URI_IDX_1, params.idx))
            .then(response => response.json())
            .then(data => {
                setStore(data);
            })
            .catch(error => {
                console.error('Error fetching store data:', error);
            });
    }
    useEffect(() => {
        fetchBike();
    }, []);

    const handleEquipmentChange = (options) => {
        setSelectedEquipmentOptions(options);
        setSelectedEquipment(options.map(option => option.value));
    }

    // TODO check if selected
    const handleCalendarChange = selectedDateMargins => {
        setSelectedDateMargins(selectedDateMargins);
        if (!isRangeLengthValid(selectedDateMargins)) {
            alert('Please select a range of up to 3 days.');
            setSelectedDateMargins([]);
        }
    };

    const isRangeLengthValid = selectedDateMargins => {
        return selectedDateMargins[1].getDate() - selectedDateMargins[0].getDate() < 3;
    };

    const handleBookClick = () => {
        if (!token) {
            console.log(':(', token)
            setOpenLoginSignInDialog(true);
        } else {
            startTransition(() => {
                navigate(URL_BOOKING_CHECKOUT.replace(':', '').replace(URL_IDX, params.idx), {
                    state: {
                        booking_data: {
                            bike: params.idx,
                            store: store.name,
                            equipment: selectedEquipment,
                            startDate: selectedDateMargins[0],
                            endDate: selectedDateMargins[1],
                        },
                        token: token
                    },
                });
            })
        }
    };

    const handleStoreClick = () => {
        console.log('bikeId', params.idx)
        startTransition(() => {
            navigate(URL_BOOKING_STORE.replace(':', '').replace(URL_IDX, params.idx), {state: {token: token}});
        })
    };

    const handleLoginClick = () => {
        startTransition(() => {
            navigate(URL_USER_LOGIN, {
                state: {
                    booking_data: {
                        bike: params.idx,
                        store: store.name,
                        equipment: selectedEquipment,
                        startDate: selectedDateMargins[0],
                        endDate: selectedDateMargins[1],
                    }
                }
            })
        })
    }

    const handleSignInClick = () => {
        startTransition(() => {
            navigate(URL_USER_REGISTER, {
                state: {
                    booking_data: {
                        bike: params.idx,
                        store: store.name,
                        equipment: selectedEquipment,
                        startDate: selectedDateMargins[0],
                        endDate: selectedDateMargins[1],
                    }
                }
            })
        })
    }

    const handleCancelClick = () => {
        setOpenLoginSignInDialog(false);
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_FILTER, {state: {token: token}})
        })
    }

    return (
        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>
                    {t('heading')}: {bike.name}
                </h1>

                { openLoginSignInDialog ?
                        <div className={'content-container'}>
                            <h3>{t('login_to_rent')}</h3>
                            <div className={'button-list'}>
                                <Button className={'accent-button'} onClick={handleLoginClick}>{t('login')}</Button>
                                <Button className={'button'} onClick={handleSignInClick}>{t('sign_in')}</Button>
                                <Button className={'button'} onClick={handleCancelClick}>{t('cancel')}</Button>
                            </div>
                        </div>
                    :
                        <>
                            <div className={'content-container'}>
                                <h2>{bike.name}</h2>
                                <img className={'img'} src={bike_img} alt="BikeImage" width={'100%'} />
                                <p>{bike.description}</p>
                                <label htmlFor='region'>{t('select_equipment')}:</label>
                                <Select
                                    isMulti
                                    placeholder={"None"}
                                    options={allEquipmentOptions}
                                    name='regions'
                                    onChange={handleEquipmentChange}
                                    className='basic-multi-select'
                                    classNamePrefix='select'
                                />
                            </div>
                            <div className={'content-container'}>
                                <h2>{t('dates_of_renting')} </h2>
                                <div className='calendar-container'>
                                    <Calendar
                                        onChange={handleCalendarChange}
                                        selectRange={true}
                                        value={selectedDateMargins} />
                                </div>
                                <div className={'button-list'}>
                                    {/*TODO make inactive if date range not selected correctly*/}
                                    <Button className={'accent-button'} onClick={handleBookClick}>{t('book')}</Button>
                                </div>
                            </div>
                            <div className={'clickable-content-container'} onClick={handleStoreClick}>
                                <h2>{store.name}</h2>
                                <p>{t('address')}: {store.address}</p>
                            </div>
                        </>
                }

                <div className={'button-list'}>
                    <Button className={'button'} onChange={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
};

export default Bike;
