import React, {startTransition, Suspense, useEffect, useState} from 'react';
import 'react-calendar/dist/Calendar.css';
import '../../Style.css';
import {Button} from 'antd';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {
    URI_MANAGER_BIKE,
    URI_MANAGER_BIKE_UPDATE,
    URI_MANAGER_STORE,
    URI_MANAGER_BIKE_DELETE,
    URI_IDX_1, URI_MANAGER_EQUIPMENT_POST
} from '../../constants/URIs';
import {URL_MANAGER_BIKE_MANAGEMENT, URL_IDX, URL_MANAGER_CONFIG} from "../../constants/URLs";
import {
    ERR_DELETING_BIKE, ERR_POSTING_EQUIPMENT,
    ERR_UPDATING_BIKE,
} from "../../constants/ErrorMessages";
import Select from "react-select";


const Bike = () =>  {
    const { t } = useTranslation('translations/admin/Bike');
    const params = useParams();
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [bike, setBike] = useState('');

    const [store, setStore] = useState('');

    const [name, setName] = useState('');
    const [image, setImage] = useState('');
    const [imageLink, setImageLink] = useState('');
    const [description, setDescription] = useState('');

    const [openEdit, setOpenEdit] = useState(false);
    const [openDeleteConfirmation, setOpenDeleteConfirmation] = useState(false);

    const [openAddExistingEquipment, setOpenAddExistingEquipment] = useState(true);

    // const [allEquipment, setAllEquipment] = useState([]);
    const [allEquipmentOptions, setAllEquipmentOptions] = useState([])
    const [selectedEquipmentOption, setSelectedEquipmentOption] = useState({});
    const [selectedEquipment, setSelectedEquipment] = useState('');
    const [newEquipment, setNewEquipment] = useState({});



    // GET bike
    const fetchBike = () => {
        fetch(URI_MANAGER_BIKE.replace(URI_IDX_1, params.idx), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                // TODO test
                console.log('data:', data)
                setBike(data);
                // setAllEquipment(data.equipment);
                setAllEquipmentOptions(data.equipment.map((equipment) => (
                    {value: equipment.equipment, label: equipment.equipment}
                )));
                console.log(data.equipment)
                console.log('allEquipmentOptions', data.equipment.map((equipment) => (
                    {value: equipment[0], label: equipment[1]}
                )))
                fetchStore(data.store);
            })
            .catch(error => { console.error('Error fetching bike data:', error); });
    };

    // GET store
    const fetchStore = (storeId) => {
        fetch(URI_MANAGER_STORE.replace(URI_IDX_1, storeId), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => { setStore(data) })
            .catch(error => { console.error('Error fetching store data:', error); })
    }

    useEffect(() => {
        fetchBike();
    }, []);

    // POST bike equipment
    const postEquipment = (equipment) => {
        let payload = {equipment: equipment}
        fetch(URI_MANAGER_EQUIPMENT_POST.replace(URI_IDX_1, params.idx), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload)
        })
            // .then((response) => {console.log('response', response); response.json(); })
            .catch((error) => {
                console.error(ERR_POSTING_EQUIPMENT, error);
            });
    }

    // PATCH bike info
    const updateBike = () => {
        const payload = {};

        if (name !== '') {
            payload.name = name;
        }
        if (imageLink !== '') {
            payload.image_link = imageLink;
        }
        if (description !== '') {
            payload.description = description;
        }
        fetch(URI_MANAGER_BIKE_UPDATE.replace(URI_IDX_1, params.idx), {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload),
        })
            .then((response) => response.json())
            .catch((error) => {
                console.error(ERR_UPDATING_BIKE, error);
            });
    }

    // DELETE bike
    const postBikeDeletion = () => {
        return fetch(URI_MANAGER_BIKE_DELETE.replace(URI_IDX_1, params.idx), {
            method: 'DELETE',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        });
    };

    const handleEditClick = () => {
        setOpenEdit(true);
    }

    const handleEquipmentChange = (option) => {
        console.log('option', option)
        setSelectedEquipmentOption(option);
        setSelectedEquipment(option.value);
    }

    const handleDeleteClick = () => {
        setOpenDeleteConfirmation(true);
    }

    const handleConfirmDeleteClick = () => {
        postBikeDeletion()
            .then(response => {
                if (response.ok) {
                    // Bike deleted successfully
                    alert(t('bike_deleted'));
                    startTransition(() => {
                        navigate(URL_MANAGER_BIKE_MANAGEMENT, {state: {token: token}})
                    })
                } else {
                    // Handle unsuccessful store deletion
                    console.error('Unsuccessful bike deletion');
                }
            })
            .catch(error => {
                console.error(ERR_DELETING_BIKE, error);
            });
    }

    const handleCancelDeleteClick = () => {
        setOpenDeleteConfirmation(false);
    }

    const handleAddNewEquipmentClick = () => {
        setSelectedEquipmentOption({});
        setSelectedEquipment('');
        setOpenAddExistingEquipment(false);
    }

    const handleAddExistingEquipmentClick = () => {
        setNewEquipment('');
        console.log('Ex. Eq.', openAddExistingEquipment)
        setOpenAddExistingEquipment(true);
    }

    const handleSubmitClick = () => {
        updateBike();
        console.log('selectedEquipment', selectedEquipment)
        if (selectedEquipment !== '') {
            postEquipment(selectedEquipment);
        } else if (newEquipment !== '') {
            postEquipment(newEquipment);
        }
        fetchBike()
        setOpenEdit(false);
    }

    const handleCancelClick = () => {
        setOpenEdit(false);
    }

    const handleNameEdit = (value) => {
        setName(value);
    }

    const handleImageEdit = (value) => {
        setImage(value)
    }

    const handleDescriptionEdit = (value) => {
        setDescription(value);
    }

    const handleStoreClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_CONFIG.replace(':', '').replace(URL_IDX, store.id), {state: {token: token}});
        })
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MANAGER_BIKE_MANAGEMENT, {state: {token: token}});
        })
    }

    return (

        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>

                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>

                { openEdit ?
                    <div className={'content-container'}>

                        <h2>{t('store_info')}</h2>

                        <h3>{t('name')}</h3>
                        <input
                            className={"text-form"}
                            type="text"
                            placeholder={bike.name}
                            onInput={e => handleNameEdit(e.target.value)}
                        />

                        <h3>{t('image')}</h3>
                        <div>
                            <img src={bike.image_link} alt='BikeImage' width={'100%'}/>
                        </div>
                        <input
                            type="file"
                            onInput={e => handleImageEdit(e.target.files[0])}
                        />

                        <h3>{t('description')}</h3>
                        <input
                            className={"text-form"}
                            type="text"
                            placeholder={bike.description}
                            onInput={e => handleDescriptionEdit(e.target.value)}
                        />

                        {/*<div className={'content-container'}>*/}
                        <h2>{t('equipment')}</h2>

                        { openAddExistingEquipment ?
                            <>
                                <h3>{t('new_equipment')}</h3>
                                <input
                                    className={'text-form'}
                                    type='text'
                                    placeholder={t('add_equipment')}
                                    onInput={e => setNewEquipment(e.target.value)}
                                />
                                <div className='button-list'>
                                    <Button className={'button'} onClick={handleAddNewEquipmentClick}>{t('add_existing_equipment')}</Button>
                                </div>
                            </>
                            :
                            <>
                                <h3>{t('existing_equipment')}</h3>
                                <Select
                                    placeholder={'None'}
                                    options={allEquipmentOptions}
                                    name='equipment'
                                    onChange={handleEquipmentChange}
                                    className='basic-multi-select'
                                    classNamePrefix='select'
                                />
                                <div className='button-list'>
                                    <Button className={'button'} onClick={handleAddExistingEquipmentClick}>{t('add_new_equipment')}</Button>
                                </div>
                            </>
                        }
                        {/*</div>*/}

                        <div className={'button-list'}>
                            <Button className={'accent-button'} onClick={handleSubmitClick}>{t('submit')}</Button>
                            <Button className={'button'} onClick={handleCancelClick}>{t('cancel')}</Button>
                        </div>
                    </div>
                    :
                    <div className={'content-container'}>
                        <h2>{bike.name}</h2>
                        <img src={bike.image_link} alt='BikeImage' width={'100%'}/>
                        <p>{bike.description}</p>
                        <p>{t('equipment')}: {bike.equipment?.map(e => e.equipment).toString()}</p>

                        { openDeleteConfirmation ?
                            <>
                                <h2>{t('delete_inquire')}</h2>
                                <div className={"button-list"}>
                                    <Button className={"accent-button"} onClick={handleCancelDeleteClick}>{t('cancel')}</Button>
                                    <Button className={"button"} onClick={handleConfirmDeleteClick}>{t('confirm_delete')}</Button>
                                </div>
                            </>
                            :
                            <div className={'button-list'}>
                                <Button className={'button'} onClick={handleEditClick}>{t('edit')}</Button>
                                <Button className={'button'} onClick={handleDeleteClick}>{t('delete')}</Button>
                            </div>
                        }
                    </div>
                }

                <div className={'clickable-content-container'} onClick={handleStoreClick}>
                    <h2>{store.name}</h2>
                    <p>{store.address}</p>
                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Bike;