import '../../Style.css';
import {Button, TimePicker} from 'antd';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {
    URI_ADMIN_BIKES, URI_ADMIN_CONFIG, URI_ADMIN_CONFIG_UPDATE,
    URI_ADMIN_STORE_DELETE,
    URI_ADMIN_STORE, URI_ADMIN_STORE_UPDATE,
    URI_BOOKING_REGIONS,
    URI_IDX_1
} from '../../constants/URIs';
import {ERR_DELETING_STORE, ERR_UPDATING_STORE, ERR_UPDATING_STORE_CONFIG} from '../../constants/ErrorMessages';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import Select from 'react-select';
import {
    URL_ADMIN_BIKE,
    URL_ADMIN_BIKE_REGISTRATION,
    URL_ADMIN_STORE_MANAGEMENT,
    URL_IDX,
    URL_MANAGER_BIKE
} from "../../constants/URLs";

const Store = () => {
    const { t } = useTranslation('translations/admin/Store');
    const params = useParams();
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [store, setStore] = useState('');
    const [config, setConfig] = useState({});

    const [name, setName] = useState('');
    const [address, setAddress] = useState('');
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [prepTime, setPrepTime] = useState('');

    const [monFrom, setMonFrom] = useState('');
    const [monTo, setMonTo] = useState('');
    const [monClosed, setMonClosed] = useState(false);

    const [tueFrom, setTueFrom] = useState('');
    const [tueTo, setTueTo] = useState('');
    const [tueClosed, setTueClosed] = useState(false);

    const [wedFrom, setWedFrom] = useState('');
    const [wedTo, setWedTo] = useState('');
    const [wedClosed, setWedClosed] = useState(false);

    const [thuFrom, setThuFrom] = useState('');
    const [thuTo, setThuTo] = useState('');
    const [thuClosed, setThuClosed] = useState(false);

    const [friFrom, setFriFrom] = useState('');
    const [friTo, setFriTo] = useState('');
    const [friClosed, setFriClosed] = useState(false);

    const [satFrom, setSatFrom] = useState('');
    const [satTo, setSatTo] = useState('');
    const [satClosed, setSatClosed] = useState(false);

    const [sunFrom, setSunFrom] = useState('');
    const [sunTo, setSunTo] = useState('');
    const [sunClosed, setSunClosed] = useState(false);

    const [openEdit, setOpenEdit] = useState(false);

    const [allRegions, setAllRegions] = useState([]);
    const [region, setRegion] = useState('');
    const [selectedRegionOption, setSelectedRegionOption] = useState({});

    const [bikes, setBikes] = useState([]);

    // GET all regions
    const fetchAllRegions = () => {
        fetch(URI_BOOKING_REGIONS, {
            headers: {
                'Content-Type': 'application/json',
                // 'Authorization': `Token ${token}`,
            }
        })
            .then(response => {
                return response.json()
            })
            .then(data => {
                setAllRegions(data)
            })
            .catch(error => {
                console.error('Error fetching regions:', error);
            });
    }

    const allRegionOptions = allRegions.map((region) => (
        {value: region[0], label: region[1]}
    ));


    // GET store
    const fetchStore = () => {
        fetch(URI_ADMIN_STORE.replace(URI_IDX_1, params.idx), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`,
            }
        })
            .then(response => response.json())
            .then(data => {
                setStore(data);
                fetchBikes(data);
            })
            .catch(error => {
                console.error('Error fetching store data:', error);
            });
    }

    // GET config
    const fetchConfig = () => {
        fetch(URI_ADMIN_CONFIG.replace(URI_IDX_1, params.idx), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(config => {

                console.log('GET_CONFIG:', config)

                if (config !== '') {

                    setConfig(JSON.parse(config))

                    setPrepTime(config?.pretime)

                    setMonFrom(formatStringToDate(config?.openhours?.mon.start))
                    setMonTo(formatStringToDate(config?.openhours?.mon.end))
                    setMonClosed(config?.openhours?.mon.opened)

                    setTueFrom(formatStringToDate(config?.openhours?.tue.start))
                    setTueTo(formatStringToDate(config?.openhours?.tue.end))
                    setTueClosed(config?.openhours?.tue.opened)

                    setWedFrom(formatStringToDate(config?.openhours?.wed.start))
                    setWedTo(formatStringToDate(config?.openhours?.wed.end))
                    setWedClosed(config?.openhours?.wed.opened)

                    setThuFrom(formatStringToDate(config?.openhours?.thu.start))
                    setThuTo(formatStringToDate(config?.openhours?.thu.end))
                    setThuClosed(config?.openhours?.thu.opened)

                    setFriFrom(formatStringToDate(config?.openhours?.fri.start))
                    setFriTo(formatStringToDate(config?.openhours?.fri.end))
                    setFriClosed(config?.openhours?.fri.opened)

                    setSatFrom(formatStringToDate(config?.openhours?.sat.start))
                    setSatTo(formatStringToDate(config?.openhours?.sat.end))
                    setSatClosed(config?.openhours?.sat.opened)

                    setSunFrom(formatStringToDate(config?.openhours?.sun.start))
                    setSunTo(formatStringToDate(config?.openhours?.sun.end))
                    setSunClosed(config?.openhours?.sun.opened)
                }
            })
            .catch(error => {
                console.error('Error fetching store config data:', error);
                setConfig(null);
            });
    };

    // GET bikes
    const fetchBikes = (store) => {
        fetch(URI_ADMIN_BIKES, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                console.log('bike_data', data)
                setBikes(data.filter((bike) => bike.store === store.id));
            })
            .catch(error => {
                console.error('Error fetching bike data:', error);
            });
    }

    useEffect(() => {
        fetchAllRegions();
        fetchStore();
        setSelectedRegionOption(allRegionOptions.find(option => option.value === store.region)?.label)
        fetchConfig();
    }, []);

    const formatStringToDate = (string) => {
        console.log("STRING: ", string)
        return string !== undefined ? new Date(null, null, null, string.slice(0, 2), string.slice(-2)) : null
    }

    const formatDateToTimeString = (date) => {
        return new Date(date).getHours().toString().padStart(2, '0')
            + ':'
            + new Date(date).getMinutes().toString().padStart(2, '0');
    }

    const getRegionOptionByValue = (value) => {
        return allRegionOptions.find(option => option.value === value)?.label;
    }

    const handleEditClick = () => {
        setOpenEdit(true);
    }

    const handleRegionChange = (option) => {
        setSelectedRegionOption(option)
        setRegion(option.value)
        console.log('VALUE:' + option.value)
    };

    const handleMonClosedChange = () => {
        setMonClosed(!monClosed);
        if (!monClosed) {
            setMonFrom(null);
            setMonTo(null);
        }
    }

    const handleTueClosedChange = () => {
        setTueClosed(!tueClosed);
        if (!tueClosed) {
            setTueFrom(null);
            setTueTo(null);
        }
    }

    const handleWedClosedChange = () => {
        setWedClosed(!wedClosed);
        if (!wedClosed) {
            setWedFrom(null);
            setWedTo(null);
        }
    }

    const handleThuClosedChange = () => {
        setThuClosed(!thuClosed);
        if (!thuClosed) {
            setThuFrom(null);
            setThuTo(null);
        }
    }

    const handleFriClosedChange = () => {
        setFriClosed(!friClosed);
        if (!friClosed) {
            setFriFrom(null);
            setFriTo(null);
        }
    }

    const handleSatClosedChange = () => {
        setSatClosed(!satClosed);
        if (!satClosed) {
            setSatFrom(null);
            setSatTo(null);
        }
    }

    const handleSunClosedChange = () => {
        setSunClosed(!sunClosed);
        if (!sunClosed) {
            setSunFrom(null);
            setSunTo(null);
        }
    }

    // PATCH store
    const updateStore = () => {
        const payload = {};

        if (name !== '') {
            payload.name = name;
        }
        if (address !== '') {
            payload.address = address;
        }
        if (region !== '') {
            payload.region = region;
        }
        if (email !== '') {
            payload.email = email;
        }
        if (phone !== '') {
            payload.phone = phone;
        }

        console.log('PAYLOAD:', payload)

        fetch(URI_ADMIN_STORE_UPDATE.replace(URI_IDX_1, params.idx), {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload),
        })
            .then((response) => response.json())
            .catch((error) => { console.error(ERR_UPDATING_STORE, error);});
    }

    // PATCH config
    const updateConfig = () => {
        const payload = {
            pretime: prepTime,
            openhours: {
                mon: {
                    opened: !monClosed,
                    start: formatDateToTimeString(monFrom),
                    end: formatDateToTimeString(monTo)
                },
                tue: {
                    opened: !tueClosed,
                    start: formatDateToTimeString(tueFrom),
                    end: formatDateToTimeString(tueTo)
                },
                wed: {
                    opened: !wedClosed,
                    start: formatDateToTimeString(wedFrom),
                    end: formatDateToTimeString(wedTo)
                },
                thu: {
                    opened: !thuClosed,
                    start: formatDateToTimeString(thuFrom),
                    end: formatDateToTimeString(thuTo)
                },
                fri: {
                    opened: !friClosed,
                    start: formatDateToTimeString(friFrom),
                    end: formatDateToTimeString(friTo)
                },
                sat: {
                    opened: !satClosed,
                    start: formatDateToTimeString(satFrom),
                    end: formatDateToTimeString(satTo)
                },
                sun: {
                    opened: !sunClosed,
                    start: formatDateToTimeString(sunFrom),
                    end: formatDateToTimeString(sunTo)
                }
            }
        }
        console.log('PATCH_CONFIG:', payload)
        fetch(URI_ADMIN_CONFIG_UPDATE.replace(URI_IDX_1, params.idx), {
            method: 'PATCH',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload),
        })
            .then((response) => {response.json(); console.log('PATCH_RESPONSE:', payload)})
            .catch((error) => { console.error(ERR_UPDATING_STORE_CONFIG, error);});
    }

    const handleConfirmEditClick = () => {
        updateStore();
        updateConfig();
        setOpenEdit(false);
    }

    const handleCancelEditClick = () => {
        setOpenEdit(false);
    }

    // POST store deletion
    const postStoreDeletion = () => {
        return fetch(URI_ADMIN_STORE_DELETE.replace(URI_IDX_1, params.idx), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        });
    }

    console.log(store.region)

    const handleDeleteClick = () => {
        postStoreDeletion()
            .then(response => {
                if (response.ok) {
                    // Store deleted successfully
                    alert(t('store_deleted'));
                    startTransition(() => {
                        navigate(URL_ADMIN_STORE_MANAGEMENT, { state: {token: token}})
                    })
                } else {
                    // Handle unsuccessful store deletion
                    console.error('Unsuccessful store deletion');
                }
            })
            .catch(error => {
                console.error(ERR_DELETING_STORE, error);
            });
    }

    const handleAddBikeClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_BIKE_REGISTRATION.replace(':', '').replace(URL_IDX, store.id), {
                state: {token: token}
            });
        })
    }

    const handleBikeClick = (bikeId) => {
        startTransition(() => {
            navigate(URL_ADMIN_BIKE.replace(':', '').replace(URL_IDX, bikeId), {
                state: {token: token}
            });
        });
    }

    const BikeList = () => {
        if (bikes.length === 0) {
            return <p>{t('no_bikes')}</p>;
        }

        return (
            <>
                {bikes.map((bike) => (
                    <div
                        key={bike.id}
                        className='clickable-content-container'
                        onClick={() => handleBikeClick(bike.id)}
                    >
                        <h2>{bike.name}</h2>
                        <img className={"img"} src={bike.image_link} alt='bikeImage'/>
                    </div>
                ))}
            </>
        );
    };

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE_MANAGEMENT, {state: {token: token}})
        })
    }

    return (
        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>

                { openEdit ?
                    <>

                        <div className={'content-container'}>
                            <h2>{t('store_info')}</h2>

                            <h3>{t('name')}</h3>
                            <input
                                className={'text-form'}
                                type='text'
                                placeholder={store.name}
                                onInput={e => setName(e.target.value)}
                            />

                            <h3>{t('address')}</h3>
                            <input
                                className={'text-form'}
                                type='text'
                                placeholder={store.address}
                                onInput={e => setAddress(e.target.value)}
                            />


                            <h3>{t('select_region')}:</h3>
                            <Select
                                placeholder={getRegionOptionByValue(store.region)}
                                options={allRegionOptions}
                                value={selectedRegionOption}
                                name='regions'
                                onChange={handleRegionChange}
                                className='basic-multi-select'
                                classNamePrefix='select'
                            />

                            <h3>{t('email')}</h3>
                            <input
                                className={'text-form'}
                                type='text'
                                placeholder={store.email}
                                onInput={e => setEmail(e.target.value)}
                            />

                            <h3>{t('phone')}</h3>
                            <input
                                className={'text-form'}
                                type='text'
                                placeholder={store.phone_number}
                                onInput={e => setPhone(e.target.value)}
                            />
                        </div>

                        <div className={'content-container'}>
                            <h2>{t('opening_hours')}</h2>

                            <h3>{t('mon')}</h3>
                            <p>{t('from')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={monFrom}
                                            onChange={(value) => setMonFrom(value)}>
                                </TimePicker>&emsp;
                                {t('to')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={monTo}
                                            onChange={(value) => setMonTo(value)}>
                                </TimePicker>&emsp;
                                {t('closed')}
                                <input type='checkbox'
                                       checked={monClosed}
                                       onChange={handleMonClosedChange}/>
                            </p>

                            <h3>{t('tue')}</h3>
                            <p>{t('from')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={tueFrom}
                                            onChange={(value) => setTueFrom(value)}>
                                </TimePicker>&emsp;
                                {t('to')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={tueTo}
                                            onChange={(value) => setTueTo(value)}>
                                </TimePicker>&emsp;
                                {t('closed')}
                                <input type='checkbox'
                                       checked={tueClosed}
                                       onChange={handleTueClosedChange}/>
                            </p>

                            <h3>{t('wed')}</h3>
                            <p>{t('from')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={wedFrom}
                                            onChange={(value) => setWedFrom(value)}>
                                </TimePicker>&emsp;
                                {t('to')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={wedTo}
                                            onChange={(value) => setWedTo(value)}>
                                </TimePicker>&emsp;
                                {t('closed')}
                                <input type='checkbox'
                                       checked={wedClosed}
                                       onChange={handleWedClosedChange}/>
                            </p>

                            <h3>{t('thu')}</h3>
                            <p>{t('from')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={thuFrom}
                                            onChange={(value) => setThuFrom(value)}>
                                </TimePicker>&emsp;
                                {t('to')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={thuTo}
                                            onChange={(value) => setThuTo(value)}>
                                </TimePicker>&emsp;
                                {t('closed')}
                                <input type='checkbox'
                                       checked={thuClosed}
                                       onChange={handleThuClosedChange}/>
                            </p>

                            <h3>{t('fri')}</h3>
                            <p>{t('from')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={friFrom}
                                            onChange={(value) => setFriFrom(value)}>
                                </TimePicker>&emsp;
                                {t('to')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={friTo}
                                            onChange={(value) => setFriTo(value)}>
                                </TimePicker>&emsp;
                                {t('closed')}
                                <input type='checkbox'
                                       checked={friClosed}
                                       onChange={handleFriClosedChange}/>
                            </p>


                            <h3>{t('sat')}</h3>
                            <p>{t('from')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={satFrom}
                                            onChange={(value) => setSatFrom(value)}>
                                </TimePicker>&emsp;
                                {t('to')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={satTo}
                                            onChange={(value) => setSatTo(value)}>
                                </TimePicker>&emsp;
                                {t('closed')}
                                <input type='checkbox'
                                       checked={satClosed}
                                       onChange={handleSatClosedChange}/>
                            </p>

                            <h3>{t('sun')}</h3>
                            <p>{t('from')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={sunFrom}
                                            onChange={(value) => setSunFrom(value)}>
                                </TimePicker>&emsp;
                                {t('to')}&emsp;
                                <TimePicker className={'time-picker'}
                                            placeholder={t('time')}
                                            format='HH:mm'
                                            value={sunTo}
                                            onChange={(value) => setSunTo(value)}>
                                </TimePicker>&emsp;
                                {t('closed')}
                                <input type='checkbox'
                                       checked={sunClosed}
                                       onChange={handleSunClosedChange}/>
                            </p>


                        </div>
                        <div className={'content-container'}>
                            <h2>{t('prep_time')}</h2>
                            {t('prep_time_in_hours')}:&emsp;
                            <input
                                className={'text-form'}
                                type='text'
                                pattern='[0-9]*'
                                value={prepTime}
                                onChange={e => setPrepTime(e.target.value)}>
                            </input>
                        </div>
                        <div className={'button-list'}>
                            <Button className={'accent-button'} onClick={handleConfirmEditClick}>{t('confirm_changes')}</Button>
                            <Button className={'button'} onClick={handleCancelEditClick}>{t('cancel')}</Button>
                        </div>
                    </>
                    :
                    <>
                        <div className={'content-container'}>
                            <h2>{t('contact_data')}</h2>
                            <p>{t('address')}: {store.address}</p>
                            <p>{t('region')}: {getRegionOptionByValue(store.region)}</p>
                            <p>{t('email')}: {store.email}</p>
                            <p>{t('phone')}: {store.phone_number}</p>

                            <h2>{t('opening_hours')}</h2>

                            { config !== null ?
                                <>
                                    <p>{t('mon')}: {config?.openhours?.mon.start} - {config?.openhours?.mon.end}</p>
                                    <p>{t('tue')}: {config?.openhours?.tue.start} - {config?.openhours?.tue.end}</p>
                                    <p>{t('wed')}: {config?.openhours?.wed.start} - {config?.openhours?.wed.end}</p>
                                    <p>{t('thu')}: {config?.openhours?.thu.start} - {config?.openhours?.thu.end}</p>
                                    <p>{t('fri')}: {config?.openhours?.fri.start} - {config?.openhours?.fri.end}</p>
                                    <p>{t('sat')}: {config?.openhours?.sat.start} - {config?.openhours?.sat.end}</p>
                                    <p>{t('sun')}: {config?.openhours?.sun.start} - {config?.openhours?.sun.end}</p>
                                </>
                                :
                                <p>{t('not_configured')}</p>
                            }

                            { config !== null ?
                                <p>{t('prep_time')}: {config?.pretime}</p>
                                :
                                <p>{t('prep_time')}: {t('not_configured')}</p>
                            }

                            <div className={'button-list'}>
                                <Button className={'accent-button'} onClick={handleEditClick}>{t('edit_store')}</Button>
                                <Button className={'button'} onClick={handleDeleteClick}>{t('delete_store')}</Button>
                                <Button className={'button'} onClick={handleAddBikeClick}>{t('add_bike')}</Button>
                            </div>
                        </div>
                        <BikeList />
                    </>
                }

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Store;