import React, {startTransition, Suspense, useEffect, useState} from "react";
import Loading from "../Loading";
import logo_img from "../../logo.png";
import {Button} from "antd";
import LanguageMenu from "../LanguageMenu";
import {useTranslation} from "react-i18next";
import {
    URI_HOST, URI_IDX_1, URI_MANAGER_BOOKING_KEY, URI_MANAGER_BOOKING,
    URI_MANAGER_CONFIRMATION,
    URI_MANAGER_LOCAL_DATA
} from "../../constants/URIs";
import {
    ERR_FETCHING_ASSURANCE_LVL,
    ERR_FETCHING_BOOKING_DATA,
    ERR_FETCHING_BOOKING_ID
} from "../../constants/ErrorMessages";
import {useLocation, useNavigate, useParams} from "react-router-dom";
import {URL_MENU} from "../../constants/URLs";


const Handout = () => {
    const { t } = useTranslation('translations/manager/Handout');
    const navigate = useNavigate();
    const params = useParams();
    const token = useLocation().state.token;

    const [bookingId, setBookingId] = useState('');
    const [bikeName, setBikeName] = useState('');
    const [startDate, setStartDate] = useState('');
    const [endDate, setEndDate] = useState('');
    const [bookingStatus, setBookingStatus] = useState([]);
    const [username, setUsername] = useState('');

    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');
    const [address, setAddress] = useState('');
    const [idNumber, setIdNumber] = useState('');
    const [assuranceLvl, setAssuranceLvl] = useState('');

    const [enteredIdNumber, setEnteredIdNumber] = useState('');

    const [userVerified, setUserVerified] = useState(false);
    const [userVerifiedBefore, setUserVerifiedBefore] = useState(false);

    // GET confirmation data
    const fetchAssuranceLvl = () => {

        fetch(URI_MANAGER_CONFIRMATION, {
            headers: {
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                setAssuranceLvl(data.user.assurance_lvl);
            })
            .catch(error => {
                console.error(ERR_FETCHING_ASSURANCE_LVL, error);
            });
    };

    // GET local data
    const fetchLocalData = () => {

        fetch(URI_MANAGER_LOCAL_DATA, {
            headers: {
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                setFirstName(data.firstName);
                setLastName(data.lastName);
                setAddress(data.address);
                setIdNumber(data.id_number);
            })
            .catch(error => {
                console.error(ERR_FETCHING_ASSURANCE_LVL, error);
            });
    }

    // GET booking id
    const fetchBookingId = () => {

        fetch(URI_MANAGER_BOOKING_KEY.replace(URI_IDX_1, params.key), {
            headers: {
                'Authorization': `Token ${token}`
            }
        })
        .then(response => response.json())
        .then(data => {
            setBookingId(data.id)
        })
        .catch(error => {
            console.error(ERR_FETCHING_BOOKING_ID, error);
        });
    }

    // GET booking
    const fetchBooking = () => {

        fetch(URI_MANAGER_BOOKING.replace(URI_IDX_1, bookingId), {
            headers: {
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                setBikeName(data.bike);
                setStartDate(data.begin);
                setEndDate(data.end);
                setBookingStatus(data.booking_status);
            })
            .catch(error => {
                console.error(ERR_FETCHING_BOOKING_DATA, error);
            });
    }

    useEffect(() => {
        fetchAssuranceLvl();
        fetchLocalData();
        fetchBookingId();
        fetchBooking();
    }, []);



    const handleIdVerificationInput = (e) => {

    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}});
        });
    }


    return (
        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>

                <div className={'content-container'}>
                    <h2>{t('booking_info')}</h2>
                    <p>{t('bike')}: {bikeName}</p>
                    <p>{t('from')}: {startDate}</p>
                    <p>{t('to')}: {endDate}</p>
                    <p>{t('client')}: {username}</p>
                </div>

                { bookingStatus.includes('R') ?
                    // bike not returned
                    <>
                        { userVerified ?
                            // user verified
                            <div className={"content-container"}>
                                <h2>{t('id_verification')}</h2>

                                <h3>{t('last_id_number_digits')}</h3>
                                <input
                                    className={"text-form"}
                                    type="text"
                                    onInput={e => handleIdVerificationInput(e)}
                                />
                            </div>

                        :
                            // user not verified
                            <div className={"content-container"}>
                                <h2>{t('renew_ass_lvl')}</h2>

                                <h3>{t('first_name')}</h3>
                                <input
                                    className={'text-form'}
                                    type='text'
                                    defaultValue={firstName}
                                    onInput={e => {setFirstName(e.target.value)}}
                                />

                                <h3>{t('last_name')}</h3>
                                <input
                                    className={'text-form'}
                                    type='text'
                                    defaultValue={lastName}
                                    onInput={e => setLastName(e.target.value)}
                                />

                                <h3>{t('address')}</h3>
                                <input
                                    className={'text-form'}
                                    type='text'
                                    defaultValue={address}
                                    onInput={e => setAddress(e.target.value)}
                                />

                                {/* user have been verified before */}
                                { userVerifiedBefore ?
                                    <>
                                        <h3>{t('last_id_number_digits')}</h3>
                                        <input
                                            className={"text-form"}
                                            type="text"
                                            onInput={e => handleIdVerificationInput(e)}
                                        />
                                    </>
                                :
                                    // user have not been verified before

                                    <>
                                        <h3>{t('id_number')}</h3>
                                        <input
                                            className={'text-form'}
                                            type='text'
                                            onInput={e => setIdNumber(e.target.value)}
                                        />
                                    </>
                                }
                            </div>
                        }
                        <div className={"button-list"}>
                            { !bookingStatus.includes('P') ?
                                // bike not handed out yet
                                <Button className={"button"}>{t('handout')}</Button>
                            :
                                // bike handed out
                                <Button className={"button"}>{t('accept')}</Button>
                            }

                        </div>
                    </>
                :
                    // bike returned
                    null
                }

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
};

export default Handout;
