import React, {startTransition, Suspense, useEffect, useState} from 'react';
import {useLocation, useNavigate, useParams} from 'react-router-dom';
import '../../Style.css';
import logo_img from '../../logo.png';
import {useTranslation} from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from '../LanguageMenu';
import {
    URI_BOOKING_BIKE_CONFIG,
    URI_BOOKING_BIKE_STORE,
    URI_BOOKING_BIKES,
    URI_HOST,
    URI_IDX_1
} from '../../constants/URIs';
import {URL_BOOKING_BIKE, URL_BOOKING_FILTER, URL_IDX} from '../../constants/URLs';
import {Button} from "antd";

const Store = () => {
    const { t } = useTranslation('translations/booking/Store');
    const navigate = useNavigate();
    const params = useParams();
    const token = useLocation().state.token

    const [store, setStore] = useState('');
    const [config, setConfig] = useState({});

    const [allBikes, setAllBikes] = useState([]);
    const [storeBikes, setStoreBikes] = useState([]);

    console.log('IDX', params.idx)

    // GET store
    const fetchStore = () => {
        fetch(URI_BOOKING_BIKE_STORE.replace(URI_IDX_1, params.idx))
            .then(response => response.json())
            .then(data => {
                console.log('DATA', data)
                setStore(data);
                fetchConfig(data.name)
            })
            .catch(error => {
                console.error('Error fetching store data:', error);
            });
    }

    // GET config
    const fetchConfig = () => {
        fetch(URI_BOOKING_BIKE_CONFIG.replace(URI_IDX_1, params.idx))
            .then(response => response.json())
            .then(data => {
                console.log('config_data', JSON.parse(data))
                setConfig(getStoreConfigByName(JSON.parse(data)));
            })
            .catch(error => {
                console.error('Error fetching store config data:', error);
            });
    };

    const getStoreConfigByName = (config, storeName) => {
        console.log(storeName)
        console.log(config)
        return config.find((c) => c.name === storeName)
    }

    // GET all bikes
    const fetchAllBikes = () => {
        fetch(URI_BOOKING_BIKES)
            .then(response => response.json())
            .then(data => {
                setAllBikes(data);
            })
            .catch(error => {
                console.error('Error fetching bikes data:', error);
            });
    };
    useEffect(() => {
        fetchStore();
        fetchAllBikes();
        setStoreBikes(allBikes.filter(bike => bike.store === store.id));
    }, []);

    const handleBikeClick = (bikeID) => {
        startTransition(() => {
            navigate(URL_BOOKING_BIKE.replace(URL_IDX, bikeID));
        });
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_BOOKING_FILTER, {state: {token: token}})
        })
    }

    const BikesList = () => {
        if (storeBikes.length === 0) {
            return <div className={'content-container'}>No bikes at this store.</div>;
        }
        return (
            <div>
                {storeBikes.map((bike) => (
                    <div className='clickable-content-container' key={bike.id}
                         onClick={() => handleBikeClick(bike.id)}>
                        <h2>{bike.name}</h2>
                        <img className={'img'} src={bike.image_link} alt='BikeImage' width={'100%'}/>
                    </div>
                ))}
            </div>
        );
    };

    return (
        <Suspense fallback={<Loading />}>

            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{store.name}</h1>
                <div className={'content-container'}>
                    <h2>{t('contact_data')}</h2>
                    <p>{t('address')}: {store.address}</p>
                    <p>{t('region')}: {store.region}</p>
                    <p>{t('email')}: {store.email}</p>
                    <p>{t('phone')}: {store.phone_number}</p>

                    <h2>{t('opening_hours')}</h2>

                    { config !== null ?
                        <>
                            <p>{t('mon')}: {config?.openhours?.mon.start} - {config?.openhours?.mon.end}</p>
                            <p>{t('tue')}: {config?.openhours?.tue.start} - {config?.openhours?.tue.end}</p>
                            <p>{t('wed')}: {config?.openhours?.wed.start} - {config?.openhours?.wed.end}</p>
                            <p>{t('thu')}: {config?.openhours?.thu.start} - {config?.openhours?.thu.end}</p>
                            <p>{t('fri')}: {config?.openhours?.fri.start} - {config?.openhours?.fri.end}</p>
                            <p>{t('sat')}: {config?.openhours?.sat.start} - {config?.openhours?.sat.end}</p>
                            <p>{t('sun')}: {config?.openhours?.sun.start} - {config?.openhours?.sun.end}</p>
                        </>
                        :
                        <p>{t('not_configured')}</p>
                    }

                    { config !== null ?
                        <p>{t('prep_time')}: {config?.pretime}</p>
                        :
                        <p>{t('prep_time')}: {t('not_configured')}</p>
                    }

                </div>

                <h2>{t('bikes')}</h2>

                <BikesList />

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Store;