import React, {startTransition, Suspense, useEffect, useState} from 'react';
import {useLocation, useNavigate} from 'react-router-dom';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import '../../Style.css';

import Select from 'react-select';
import {Button} from "antd";
import logo_img from "../../logo.png";
import {useTranslation} from "react-i18next";
import Loading from "../Loading";
import LanguageMenu from "../LanguageMenu";
import {
    URI_BOOKING_AVAILABILITIES,
    URI_BOOKING_BIKE_STORE,
    URI_BOOKING_BIKES,
    URI_BOOKING_CONFIG,
    URI_BOOKING_REGIONS,
    URI_IDX_1
} from "../../constants/URIs";
import {URL_BOOKING_BIKE, URL_IDX, URL_MENU} from "../../constants/URLs";


function BookingProcess() {
    const { t } = useTranslation('translations/booking/Filter');
    const navigate = useNavigate();
    const token = useLocation().state?.token;

    const [allRegions, setAllRegions] = useState([]);

    const [selectedRegionOptions, setSelectedRegionOptions] = useState([]);
    const [selectedRegions, setSelectedRegions] = useState([]);

    const [selectedDateMargins, setSelectedDateMargins] = useState([]);

    const [bikes, setBikes] = useState([]);

    const [bikesStoresData, setBikesStoresData] = useState([]);

    const [config, setConfig] = useState({});

    const [availabilities, setAvailabilities] = useState([]);

    const [availableBikesData, setAvailableBikesData] = useState([]);

    // GET all regions
    const fetchAllRegions = () => {
        fetch(URI_BOOKING_REGIONS)
            .then(response => {
                return response.json()
            })
            .then(data => {
                setAllRegions(data)
            })
    }
    useEffect(() => {
        fetchAllRegions()
    }, [])

    const allRegionOptions = allRegions.map((region) => (
        {value: region[0], label: region[1]}
    ));

    const fetchAllBikes = async () => {
        try {
            const response = await fetch(URI_BOOKING_BIKES);
            const data = await response.json();
            setBikes(data);
        } catch (error) {
            console.error('Error fetching bikes data:', error);
        }
    };

    const getData = async (url) => {
        try {
            const response = await fetch(url);
            if (!response.ok) {
                throw new Error('Network response was not ok');
            }
            const data = await response.json();
            return data;
        } catch (error) {
            console.error('Error fetching data:', error);
            throw error;
        }
    };

    const fetchAllStores = async () => {
        try {
            let storeRequests = bikes.map((bike) => getData(URI_BOOKING_BIKE_STORE.replace(URI_IDX_1, bike.id)));
            const storeData = await Promise.all(storeRequests);
            let data = bikes.map((x, i) => ({ bike: x, store: storeData[i] }));
            setBikesStoresData(data);
        } catch (error) {
            console.error('Error fetching store data:', error);
        }
    };

    const fetchWholeConfig = () => {
        fetch(URI_BOOKING_CONFIG)
            .then(response => {
                return response.json()
            })
            .then(data => {
                setConfig(JSON.parse(data))
                console.log('config', config)
                console.log('config', config)

            })
    }

    useEffect(() => {
        fetchAllBikes();
    }, []);

    useEffect(() => {
        if (bikes.length > 0) {
            fetchAllStores();
            fetchWholeConfig();
        }
    }, [bikes]);


    // GET all availabilities
    const fetchAllAvailabilities = () => {
        fetch(URI_BOOKING_AVAILABILITIES)
            .then(response => {
                return response.json()
            })
            .then(data => {
                setAvailabilities(data)
                console.log('AVAILABILITIES_DATA:', data)
            })
    }
    useEffect(() => {
        fetchAllAvailabilities()
    }, [])

    /////////////////////////////////// navigation //////////////////////////////////////////////////

    const handleBikeClick = (bikeID) => {

        startTransition(() => {
            navigate(URL_BOOKING_BIKE.replace(':', '').replace(URL_IDX, bikeID), {state : {token: token}});
        });
    };


    /////////////////////////////////// handle filter input ////////////////////////////////////////////////


    const handleRegionChange = (selection) => {
        setSelectedRegionOptions(selection)
        setSelectedRegions(selection.value)
    };

    const handleCalendarChange = (margins) => {

        console.log('margins', margins)

        if (isRangeLengthValid(margins)) {
            setSelectedDateMargins(margins);
        } else {
            alert('Please select a range of up to 3 days.');
            setSelectedDateMargins([]);
        }
    };

    const isRangeLengthValid = (selectedDateMargins) => {
        return selectedDateMargins[1].getDate() - selectedDateMargins[0].getDate() < 3;
    };

    const handleFilterClick = () => {
        let regions = selectedRegionOptions.map(region => region.value)
        console.log('selectedRegionOptions', selectedRegionOptions)
        console.log('regions (1)', regions)
        updateBikeList(regions, selectedDateMargins);
    }


    /////////////////////////////////// update bike list /////////////////////////////////////////


    const updateBikeList = (regions, dateMargins) => {

        let tmp = [];

        // TODO check if date is in the future
        // TODO check prep time

        bikesStoresData.forEach(bikeData =>
            isBikeLocatedAtRegions(bikeData, regions)
            && isBikeAvailableOnDates(bikeData, dateMargins)
            && isStoreOpenedOnDateMargins(bikeData.store, dateMargins) ?
            tmp.push(bikeData) : null
        )
        setAvailableBikesData(tmp);
    };

    const isBikeLocatedAtRegions = (bikeData, regions) => {
        return regions.some(region => bikeData.store.region === region);
    }

    const isBikeAvailableOnDates = (bikeData, dateMargins) => {
        const availabilities = getAvailabilitiesByBike(bikeData);
        let available = false;
        availabilities.forEach((availability) => {
            let statusList = availability.availability_status.map(e => e.availability_status);
            if (dateMargins[0] >= new Date(availability.from_date)
                && dateMargins[1] <= new Date(availability.until_date)
                && statusList.some(e => e === 'Available')) {
                available = true;
            }
        })
        return available;
    }

    const isStoreOpenedOnDateMargins = (store, dateMargins) => {
        let storeConfig = getStoreConfigByName(store.name);
        let openingDays = getOpeningDaysFromConfig(storeConfig);

        console.log('openingDays', openingDays)
        console.log('dateMargins[0].getDay()', dateMargins[0].getDay())
        console.log('dateMargins[1].getDay()', dateMargins[1].getDay())
        console.log(openingDays.includes(dateMargins[0].getDay()))
        console.log(openingDays.includes(dateMargins[1].getDay()))

        return openingDays.includes(dateMargins[0].getDay()) && openingDays.includes(dateMargins[1].getDay())
    }

    const getStoreConfigByName = (storeName) => {
        return config.find((c) => c.name === storeName)
    }

    const getOpeningDaysFromConfig = (config) => {
        let openhours = config.openhours;

        let days = [];
        let count = 1;
        for (let x in openhours) if (openhours.hasOwnProperty(x)) {
            if (openhours[x].opened) {
                days.push(count)
            }
            count++;
        }

        console.log(days)

        return days
    }

    const getAvailabilitiesByBike = (bikeData) => {
        let tmp = [];
        availabilities.forEach(availability => availability.bike === bikeData.bike.id ? tmp.push(availability) : null)
        console.log(tmp)
        return tmp;
    }


    ////////////////////////////////// components ////////////////////////////////////////////

    const BikeList = () => {
        if (availableBikesData.length === 0) {
            return <p>{t('no_bikes')}</p>;
        }

        return (
            <>
                {availableBikesData.map((bikeData) => (
                    <div
                        key={bikeData.bike.id}
                        className='clickable-content-container'
                        onClick={() => handleBikeClick(bikeData.bike.id)}
                    >
                        <h2>{bikeData.bike.name}</h2>
                        <p>{bikeData.store.address}</p>
                        <img className={"img"} src={bikeData.bike.image_link} alt='BikeImage' width={"100%"}/>
                    </div>
                ))}
            </>
        );
    };

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_MENU, {state: {token: token}})
        })
    }


    ////////////////////////////////// return /////////////////////////////////////


    return (
        <Suspense fallback={<Loading />}>

            <div className='view-container'>
                <img className={"logo-img"} src={logo_img} alt={"logo"}></img>
                <h1>{t('heading')}</h1>
                <div className={"content-container"}>
                    <div className='form-field'>
                        <label htmlFor='region'>{t('select_regions')}:</label>
                        <Select
                            isMulti
                            placeholder={"None"}
                            options={allRegionOptions}
                            name='regions'
                            onChange={handleRegionChange}
                            className='basic-multi-select'
                            classNamePrefix='select'
                        />
                    </div>

                    <div className='form-field'>
                        <label htmlFor='dateRange'>{t('select_rental_days_range')}:</label>
                    </div>
                    <div className='calendar-container'>
                        <Calendar
                            onChange={handleCalendarChange}
                            selectRange={true}
                            value={selectedDateMargins}
                        />
                    </div>
                    <div>
                        {/*TODO make inactive if date range not selected correctly*/}
                        <Button className='accent-button' onClick={handleFilterClick} >{t('filter')}</Button>
                    </div>
                </div>

                <BikeList />

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />

            </div>
        </Suspense>
    );
}

export default BookingProcess;


