import {useLocation, useNavigate} from "react-router-dom";
import {Button} from "antd";
import '../../Style.css';
import logo_img from "../../logo.png";
import React, {Suspense, useEffect, useState} from "react";
import {useTranslation} from "react-i18next";
import LanguageMenu from "../LanguageMenu";
import Loading from "../Loading";
import {
    URI_BOOKING_BIKE_BOOKING,
    URI_BOOKING_BIKE_STORE,
    URI_BOOKING_BIKE,
    URI_HOST,
    URI_IDX_1,
} from "../../constants/URIs";
import {URL_BOOKING_FILTER, URL_BOOKING_BIKE, URL_IDX} from "../../constants/URLs";


const BookingCheckout = () => {
    const { t } = useTranslation('translations/booking/BookingCheckout');
    const navigate = useNavigate();

    const [bike, setBike] = useState('');
    const [store, setStore] = useState('');

    const state = useLocation().state;
    const token = state?.token;
    const bookingData = state?.booking_data;

    console.log(bookingData)

    // GET bike
    const fetchBike = () => {
        fetch(URI_BOOKING_BIKE.replace(URI_IDX_1, bookingData.bike))
            .then(response => {
                return response.json()
            })
            .then(data => {
                setBike(data)
            })
    }

    // GET store
    const fetchStore = () => {
        fetch(URI_BOOKING_BIKE_STORE.replace(URI_IDX_1, bookingData.bike))
            .then(response => {
                return response.json()
            })
            .then(data => {
                setStore(data)
            })
    }

    useEffect(() => {
        fetchBike()
        fetchStore()
    }, [])

    // POST booking
    const postNewBooking = () => {
        let payload = {
            begin: bookingData.startDate.toISOString().split('T')[0],
            end: bookingData.endDate.toISOString().split('T')[0],
            equipment: bookingData.equipment
        };
        console.log('token', token)
        fetch(URI_BOOKING_BIKE_BOOKING.replace(URI_IDX_1, bookingData.bike), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload)
        })
            .then(response => {
                if (response.ok) {
                    alert('Booking succesful')
                    return response.json();
                } else {
                    // If the request was not successful, handle errors here
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
    }

    const handleConfirm = () => {
        postNewBooking();
        navigate(URL_BOOKING_FILTER, {
            state: {
                token: token
            }
        });
    }

    const handleCancel = () => {
        navigate(URL_BOOKING_BIKE.replace(URL_IDX, bookingData.bikeID), {
            state: {
                token: token
            }
        });
    }

    return (

        <Suspense fallback={<Loading />}>
            <div className={"view-container"}>
                <img className={"logo-img"} src={logo_img} alt={"logo"}></img>
                <h1>{t('heading')}</h1>
                <div className={"content-container"}>
                    <h2>{t('wanna_confirm')}</h2>
                    <p>{t('bike')}: {bike.name}</p>
                    <p>{t('store')}: {store.name}</p>
                    { bookingData.equipment.length !== 0 ?
                        <p>{t('equipment')}: {bookingData.equipment}</p>
                        :
                        <p>{t('equipment')}: {t('none')}</p>
                    }
                    <p>{t('from')}: {bookingData.startDate.toLocaleDateString('de-DE')}</p>
                    <p>{t('to')}: {bookingData.endDate.toLocaleDateString('de-DE')}</p>
                </div>
                <div className={"button-list"}>
                    <Button className={"accent-button"} onClick={handleConfirm}>{t('confirm_booking')}</Button>
                    <Button className={"button"} onClick={handleCancel}>{t('cancel')}</Button>
                </div>
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default BookingCheckout;