import {Button} from 'antd';
import '../../Style.css';
import logo_img from '../../logo.png';
import React, {startTransition, Suspense, useEffect, useState} from 'react';

import { useTranslation } from 'react-i18next';
import Loading from '../Loading';
import LanguageMenu from "../LanguageMenu";
import {useLocation, useNavigate, useParams} from "react-router-dom";
import {URI_ADMIN_BAN, URI_IDX_1, URI_IDX_2, URI_USER_EMAIL_CONFIRMATION} from "../../constants/URIs";
import {URL_HOME} from "../../constants/URLs";


const EmailVerification = () => {
    const { t } = useTranslation('translations/user/EmailVerification');
    const navigate = useNavigate();
    const params = useParams();
    const id = params.id;
    const key = params.key;

    const [verificationSuccessful, setVerificationSuccessful] = useState(false);

    // TODO test
    // POST email verification
    const postVerification = () => {
        fetch(URI_USER_EMAIL_CONFIRMATION.replace(URI_IDX_1, id).replace(URI_IDX_2, key))
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    // If the request was not successful, handle errors here
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
    }

    useEffect(() => {
        postVerification()
    }, []);

    const handleOkClick = () => {
        startTransition(() => {
            navigate(URL_HOME);
        })
    }

    return (
        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>

                { verificationSuccessful ?
                    <>
                        <h2>{t('email_verification_successful')}</h2>
                        <div className={'button-list'}>
                            <Button className={'accent-button'} onClick={handleOkClick}>{t('ok')}</Button>
                        </div>
                    </>
                    :
                    <>
                        <h2>{t('something_went_wrong')}</h2>
                    </>
                }
                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default EmailVerification;