
import React, {startTransition, Suspense, useEffect, useState} from "react";
import 'react-calendar/dist/Calendar.css';
import '../../Style.css';
import {Button} from "antd";
import logo_img from "../../logo.png";
import {useTranslation} from "react-i18next";
import {useLocation, useNavigate, useParams} from "react-router-dom";
import Loading from "../Loading";
import LanguageMenu from "../LanguageMenu";
import {
    URI_ADMIN_BIKE_POST, URI_ADMIN_BIKES, URI_ADMIN_STORE,
    URI_ADMIN_STORES,
    URI_HOST, URI_IDX_1,
} from "../../constants/URIs";
import {URL_ADMIN_BIKE_MANAGEMENT, URL_ADMIN_STORE, URL_IDX} from "../../constants/URLs";
import Select from "react-select";


const Bike = () =>  {
    const { t } = useTranslation('translations/admin/BikeRegistration');
    const navigate = useNavigate();
    const params = useParams();
    const token = useLocation().state.token;

    const [store, setStore] = useState('');
    const [name, setName] = useState('');
    const [imageLink, setImageLink] = useState('');
    const [description, setDescription] = useState('');

    // GET store
    const fetchStore = () => {
        fetch(URI_ADMIN_STORE.replace(URI_IDX_1, params.idx), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`,
            }
        })
            .then(response => response.json())
            .then(data => {
                console.log('data', data)
                setStore(data);
            })
            .catch(error => {
                console.error('Error fetching store data:', error);
            });
    }
    useEffect(() => {
        fetchStore();
    }, []);


    // POST new bike
    const addBike = () => {
        let payload = {
            name: name,
            description: description,
            image_ink: imageLink
        };
        fetch(URI_ADMIN_BIKE_POST.replace(URI_IDX_1, store.id), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            },
            body: JSON.stringify(payload)
        })
            .then(response => {
                if (response.ok) {
                    return response.json();
                } else {
                    // If the request was not successful, handle errors here
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
    }

    const handleNameInput = (value) => {
        setName(value);
    }

    const handleImageLinkInput = (value) => {
        setImageLink(value)
    }

    const handleDescriptionInput = (value) => {
        setDescription(value)
    }

    const handleAddClick = () => {
        addBike();
        console.log('store', store)
        startTransition(() => {
            navigate(URL_ADMIN_STORE.replace(':', '').replace(URL_IDX, store.id), {state: {token: token}})
        })
    }

    const handleCancelClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE.replace(':', '').replace(URL_IDX, store.id), {state: {token: token}})
        })
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE.replace(':', '').replace(URL_IDX, store.id), {state: {token: token}})
        })
    }

    return (

        <Suspense fallback={<Loading />}>
            <div className={"view-container"}>

                <img className={"logo-img"} src={logo_img} alt={"logo"}></img>
                <h1>{t('heading')}</h1>

                <div className={"content-container"}>
                    <h3>{t('name')}</h3>
                    <input
                        className={'text-form'}
                        type='text'
                        onInput={e => handleNameInput(e.target.value)}
                    />

                    <h3>{t('image')}</h3>
                    <input
                        className={'text-form'}
                        type='file'
                        onInput={e => handleImageLinkInput(e.target.value)}
                    />

                    <h3>{t('description')}</h3>
                    <input
                        className={'large-text-form'}
                        type='text'
                        onInput={e => handleDescriptionInput(e.target.value)}
                    />

                    <div className={'button-list'}>
                        <Button className={'accent-button'} onClick={handleAddClick}>{t('add_bike')}</Button>
                        <Button className={'button'} onClick={handleCancelClick}>{t('cancel')}</Button>
                    </div>

                </div>

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Bike;