
import '../../Style.css';
import {Button} from 'antd';
import React, {startTransition, Suspense, useEffect, useState} from 'react';
import logo_img from '../../logo.png';
import bike_img from '../../bike.png';
import Loading from '../Loading';
import {useTranslation} from 'react-i18next';
import {useLocation, useNavigate, useParams} from "react-router-dom";
import LanguageMenu from "../LanguageMenu";
import {
    URI_ADMIN_BIKE,
    URI_ADMIN_BOOKING,
    URI_ADMIN_BOOKING_CANCEL,
    URI_ADMIN_STORE,
    URI_HOST,
    URI_IDX_1
} from "../../constants/URIs";
import {URL_ADMIN_BIKE, URL_ADMIN_BOOKING_MANAGEMENT, URL_ADMIN_STORE, URL_IDX, URL_MENU} from "../../constants/URLs";

const Booking = () => {
    const { t } = useTranslation('translation/admin/Booking');
    const params = useParams();
    const navigate = useNavigate();
    const token = useLocation().state.token;

    const [booking, setBooking] = useState('');
    const [bike, setBike] = useState('');
    const [store, setStore] = useState('');

    const [statusList, setStatusList] = useState([]);

    // GET booking
    const fetchBooking = () => {
        fetch(URI_ADMIN_BOOKING.replace(URI_IDX_1, params.idx), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => {
                console.log('booking_data', data);
                setBooking(data);
                // console.log(data.booking_status)
                setStatusList(data.booking_status.map(e => e.booking_status));
                fetchBike(data.bike);
            })
            .catch(error => { console.error('Error fetching booking data:', error); });
    }

    // GET bike
    const fetchBike = (bikeId) => {
        console.log('token', token)
        fetch(URI_ADMIN_BIKE.replace(URI_IDX_1, bikeId), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => { setBike(data); fetchStore(data.store); console.log('bike_data', data)})
            .catch(error => { console.error('Error fetching bike data:', error); });
    };

    // GET store
    const fetchStore = (storeId) => {
        console.log('storeId', storeId)
        fetch(URI_ADMIN_STORE.replace(URI_IDX_1, storeId), {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => response.json())
            .then(data => { setStore(data); console.log('store_data', data)})
            .catch(error => { console.error('Error fetching store data:', error); })
    }

    useEffect(() => {
        fetchBooking();
    }, []);

    // POST booking cancellation
    const cancelBooking = () => {
        fetch(URI_ADMIN_BOOKING_CANCEL.replace(URI_IDX_1, params.idx), {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Token ${token}`
            }
        })
            .then(response => {
                if (response.ok) {
                    return response
                } else {
                    // If the request was not successful, handle errors here
                    return response.json().then(errorData => {
                        throw new Error(errorData.message);
                    });
                }
            })
    }

    const handleBikeClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_BIKE.replace(':', '').replace(URL_IDX, bike.id), {state: {token: token}});
        })
    }

    const handleStoreClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_STORE.replace(':', '').replace(URL_IDX, store.id), {state: {token: token}});
        })
    }

    const handleCancelBookingClick = () => {
        cancelBooking();
        startTransition(() => {
            navigate(URL_ADMIN_BOOKING_MANAGEMENT, {state: {token: token}})
        })
    }

    const handleBackClick = () => {
        startTransition(() => {
            navigate(URL_ADMIN_BOOKING_MANAGEMENT, {state: {token: token}})
        })
    }

    return (
        <Suspense fallback={<Loading />}>
            <div className={'view-container'}>
                <img className={'logo-img'} src={logo_img} alt={'logo'}></img>
                <h1>{t('heading')}</h1>
                <div className={'content-container'}>
                    <h2>{t('booked_dates')}</h2>
                    {/*<p>{t('user')}: {booking.user.username}</p>*/}
                    <p>{t('from')}: {booking.begin}</p>
                    <p>{t('to')}: {booking.end}</p>
                    <p>{t('equipment')}: {booking.equipment?.map(e => e.equipment).toString()}</p>
                    <p>{t('status')}: {statusList.toString()}</p>
                    <p>{t('comment')}: {booking.comment}</p>
                </div>
                <div className={'clickable-content-container'} onClick={handleBikeClick}>
                    <h2>{bike.name}</h2>
                    <img className={'img'} src={bike.img_src} alt="BikeImage" width={'100%'}></img>
                </div>
                <div className={'clickable-content-container'} onClick={handleStoreClick}>
                    <h2>{store.name}</h2>
                    <p>{store.address}</p>
                </div>

                { statusList.includes('Cancelled') ?
                    null
                    :
                    <div className={'button-list'}>
                        <Button className={'accent-button'} onClick={handleCancelBookingClick}>{t('cancel_booking')}</Button>
                    </div>
                }

                <div className={'button-list'}>
                    <Button className={'button'} onClick={handleBackClick}>{t('back')}</Button>
                </div>

                <LanguageMenu />
            </div>
        </Suspense>
    );
}

export default Booking;